/** USER REDUCER
 * @param {import('../typings/root').User} state
 * @param {{type: string, payload: import('../typings/root').User}} action
 */export const user = (state = {}, action) => {
  switch (action.type) {
    case 'user:login':
      return state.update({
        $set: action.payload,
      });

    case 'refresh':
      return state.update({
        token: {
          $set: action.payload.token,
        },
      });

    case 'user:logout':
      return state.update({
        $set: {},
      });

    default: return state;
  }
};

/** PRODUCTS REDUCER
 * @param {import('../typings/root').Product[]} state
 * @param {{type: String, payload: import('../typings/root').Product}} action
 */export const products = (state = [], action) => {
  switch (action.type) {
    case 'products:mount':
      return state.update({
        $set: action.payload,
      });

    case 'products:create':
      return state.update({
        $unshift: [action.payload],
      });

    case 'products:update':
      return state.update({
        [state.findIndex((s) => s.uuid === action.payload.uuid)]: {
          $set: action.payload,
        },
      });

    case 'products:will:delete':
      return state.update({
        [state.findIndex((s) => s.uuid === action.payload.uuid)]: {
          $merge: {
            delete: action.payload.delete,
          },
        },
      });

    case 'products:delete':
      return state.update((x) => x.filter((s) => s.uuid !== action.payload.uuid));

    default: return state;
  }
};

